import React from "react";

function generateTable(data,user,saledetails) {
  console.log(user);
  const total = data.reduce((sum, { totalkg }) => sum + Number(totalkg), 0);
  const nettotal = data.reduce((sum, { gross_total }) => sum + Number(gross_total), 0);
  const html = `
    <table>
      <thead>
        <tr>
        <th>SL</th>
          <th>LOT</th>
          <th>INVOICE</th>
          <th>FACTORY</th>
          <th>Warehouse</th>
          <th>GRADE</th>
          <th>NET.W</th>
          <th>PKG</th>
          <th>GROSS TOTAL</th>
          <th>SAMPLE</th>
          <th>NET TOTAL</th>
          <th>VALUE</th>
          <th>CATEGORY</th>
        </tr>
      </thead>
      <tbody>
        ${data.map(({ lot, invoice, factory_name,warehouse_name,grade,net_weight,pkgs,totalkg,price,category,sample_c,gross_total },index) => `
          <tr>
          <td>${index+1}</td>
            <td>${lot}</td>
            <td>${invoice}</td>
            <td>${factory_name}</td>
            <td>${warehouse_name}</td>
            <td>${grade}</td>
            <td>${net_weight}</td>
            <td>${pkgs}</td>
            <td>${totalkg}</td>
            <td>${sample_c}</td>
            <td>${gross_total}</td>
            <td>${price}</td>
            <td>${category}</td>
          </tr>
        `).join('')}
      </tbody>
      <tfoot>
      <tr>
        <td colspan="8"></td>
        <td>${total} KG</td>
        <td></td>
        <td>${nettotal} KG</td>
        <td></td>
        <td></td>
      </tr>
    </tfoot>
    </table>
  `;

  const newWindow = window.open('');
  newWindow.document.write(`
    <html>
      <head>
        <title>List</title>
        <link rel="icon"  type="image/png" href="/ttab.png" />
        <style>
        .table-wrapper {
          margin: 0 auto;
          width: 1100px;
          padding: 20px;
          background-color: #ffffff;
          
      
      }
      body{
        font-family: 'Courier New', Courier, monospace;
        color: #37474F;
  background: #757677;
  font-size: 12px;
      }
          table {
            margin: 0 auto;

            border-collapse: collapse;
           
          }
          th, td {
            padding: 5px;
            border: 1px solid black;
          }
          td {
            white-space: nowrap;
          }
          .header {
         
            padding: 10px;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        /* Icon in the first row */
        .icon {
            width: 60px;
            height: 60px;
               }

        /* Span Container */
        .span-container {
            display: flex;
            flex-direction: column;
            align-items: center; /* Center the container horizontally */
        }

        /* Individual Spans */
        .span1, .span2, .span3 {
            padding: 2px 10px;
            text-align: center;
        }

        .span1 {
            font-size: 26px;
            font-weight:bold;
        }

        .span2, .span3 {
            font-size: 14px;
        }
        </style>
      </head>
      <body>
      <div class="table-wrapper">
      <div class="header">
      <!-- Icon in the first row -->
      <div class="icon">
      <img class="icon" src="/ttab.png" alt="Icon Image" />
      </div>

      <!-- Span Container -->
      <div class="span-container">
          <span class="span1">${user}</span>
          <span class="span2">Season :${saledetails[0].season}</span>
          <span class="span3">Sale No : ${saledetails[0].sale_no}</span>
      </div>
      <div></div>
  </div>
          <hr>
        ${html}
        </div>
      </body>
    </html>
  `);
}

const CatalogueList = ({ data,user,saledetails }) => {
  const handleClick = () => {
    generateTable(data,user,saledetails)
  };

  return <button className="btn btn-primary mb-2 w-100 text-center" onClick={handleClick}>List</button>;
};

export default CatalogueList;
