import React from "react";

function generateTable(data, user,saledetails) {
  const total = data.reduce((sum, { totalkg }) => sum + Number(totalkg), 0);
  const totalAmount = data.reduce(
    (sum, { gross_total, maximum_bid }) => sum + gross_total * maximum_bid,
    0
  );
  const decimalTotalamount = new Intl.NumberFormat("en-IN", {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  }).format(totalAmount);
  const decimalVatamount = new Intl.NumberFormat("en-IN", {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2,
  }).format(totalAmount*15/100);
  const timestamp = new Date().toLocaleString();
  const html = `
    <table>
      <thead>
        <tr>
          <th class="align-center">SL</th>
          <th class="align-center">LOT</th>
          <th class="align-center">INVOICE</th>
          <th class="align-center">FACTORY</th>
          <th class="align-center">Warehouse</th>
          <th class="align-center">GRADE</th>
          <th class="align-right">NET.W</th>
          <th class="align-right">PKG</th>
          <th class="align-right">TOTAL</th>
          <th class="align-right">VALUE</th>
          <th class="align-right">M.VALUE</th>
          <th class="align-right">AMOUNT</th>
          <th class="align-right">VAT (15%)</th>
          <th class="align-center">BIDDER</th>
          <th class="align-center">CATEGORY</th>
        </tr>
      </thead>
      <tbody>
        ${data
          .map(
            (
              {
                lot,
                invoice,
                factory_name,
                gross_total,
                warehouse_name,
                grade,
                net_weight,
                pkgs,
                totalkg,
                price,
                maximum_bid,
                buyer_name,
                category,
                sold_status,
              },
              index
            ) => `
          <tr>
            <td class="align-center">${index + 1}</td>
            <td class="align-center">${lot}</td>
            <td class="align-center">${invoice}</td>
            <td class="align-center">${factory_name}</td>
            <td class="align-center">${warehouse_name}</td>
            <td class="align-center">${grade}</td>
            <td class="align-right">${net_weight}</td>
            <td class="align-right">${pkgs}</td>
            <td class="align-right">${gross_total}</td>
            <td class="align-right">${price}</td>
            <td class="align-right">${maximum_bid}</td>
            <td class="align-right ${
              maximum_bid > 100 ? "green-text" : "red-text"
            }">${new Intl.NumberFormat("en-IN", {
              minimumFractionDigits: 2,
              maximumFractionDigits: 2,
            }).format(gross_total * maximum_bid)}</td>
            <td class="align-right ${
              maximum_bid > 100 ? "green-text" : "red-text"
            }">${new Intl.NumberFormat("en-IN", {
              minimumFractionDigits: 2,
              maximumFractionDigits: 2,
            }).format(gross_total * maximum_bid * 15/100)}</td>
            <td class="align-center">${buyer_name}</td>
            <td class="align-center">${category}</td>
          </tr>
        `
          )
          .join("")}
      </tbody>
      <tfoot>
        <tr>
          <td colspan="8"></td>
          <td class="align-right total">${total} Kg</td>
          <td></td>
          <td></td>
          <td class="align-right total">${decimalTotalamount}</td>
          <td class="align-right total">${decimalVatamount}</td>
          <td></td>
          <td></td>
        </tr>
      </tfoot>
    </table>
  `;

  const newWindow = window.open("");
  newWindow.document.write(`
    <html>
      <head>
        <title>Sold Rpoert</title>
        <title>List</title>
        <link rel="icon"  type="image/png" href="/ttab.png" />
        <style>
          .table-wrapper {
            margin: 0 auto;
            width: 1400px;
            padding: 20px;
            background-color: #ffffff;
          }
          body {
            font-family: "Courier New", Courier, monospace;
            color: #000;
            background: #757677;
            font-size: 12px;
          }
          table {
            margin: 0 auto;
            border-collapse: collapse;
          }
          th,
          td {
            padding: 5px;
            border: 1px solid black;
            text-align: center;
          }
          th.align-right,
          td.align-right {
            text-align: right;
          }
          .total {
            font-weight:bold;
            color: purple;
          }
          .green-text {
            font-weight:400;
            color: purple;
          }
          .red-text {
            color: red;
          }
          .season{
            display: flex;
            align-items: space-between;
            justify-content: center;
          }
      
        .header {
         
          padding: 10px;
          display: flex;
          align-items: center;
          justify-content: space-between;
      }

      /* Icon in the first row */
      .icon {
          width: 60px;
          height: 60px;
             }

      /* Span Container */
      .span-container {
          display: flex;
          flex-direction: column;
          align-items: center; /* Center the container horizontally */
      }

      /* Individual Spans */
      .span1, .span2, .span3 {
          padding: 2px 10px;
          text-align: center;
      }

      .span1 {
          font-size: 26px;
          font-weight:bold;
      }

      .span2, .span3 {
          font-size: 14px;
      }
      </style>
    </head>
    <body>
    <div class="table-wrapper">
    <div class="header">
    <!-- Icon in the first row -->
    <div class="icon">
    <img class="icon" src="/ttab.png" alt="Icon Image" />
    </div>

    <!-- Span Container -->
    <div class="span-container">
        <span class="span1">${user}</span>
        <span class="span2">Season :${saledetails[0].season}</span>
        <span class="span3">Sale No : ${saledetails[0].sale_no}</span>
    </div>
    <div></div>
</div>
        <hr>
          ${html}
        </div>
      </body>
    </html>
  `);
}

const SoldReport = ({ data, user,saledetails }) => {
  const handleClick = () => {
    generateTable(data, user,saledetails);
  };

  return (
    <button
      className="btn btn-primary mb-2 w-100 d-flex justify-content-between align-items-center"
      onClick={handleClick}
    >
     
      <span className="text-center mx-auto"> Sold Report</span>
  {/* <span className="icon my-auto">&#x1F512;</span> */}
    </button>
  );
};

export default SoldReport;
