import React, { useEffect, useMemo, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import OpenHTMLButton from "../components/Report/OpenHTMLButton";
import CatalogueList from "../components/Report/CatalougueList";
import AuctionReport from "../components/Report/AuctionReport";
import SoldReport from "../components/Report/SoldReport";
import ValuationReport from "../components/Report/ValuationReport";
import InvoiceGenerate from "../components/Report/InvoiceGenerate";
import TestingReport from "../components/Report/TestingReport";
import AuctionSummaryBuyer from "../components/Report/AuctionSummaryBuyer";
import FactorywiseSummary from "../components/Report/FactorywiseSummary";
import FactorySold from "../components/Report/FactorySold";
import BrokersSummary from "../components/Report/BrokersSummary";
import BiddersoldReport from "../components/Report/BiddersoldReport";
import { getReportdata, getAllfactory,getAllsaleno } from "../store/catalogueSlice";

const ReportAll = () => {
  const dispatch = useDispatch();
  const usersString = localStorage.getItem("users");
  const users = JSON.parse(usersString);
  const [selectedBrokers, setSelectedBrokers] = useState("");
  const [selectSell, setSelectSell] = useState(0);
  const [selectedBrokersname, setSelectedBrokersname] = useState("");
  const [selectedDataArray, setSelectedDataArray] = useState([]);

  const { factory_list, edit, report_list,sale_list } = useSelector(
    (state) => state.catalogue
  );
// console.log(selectedDataArray);
  const catList = useMemo(() => {
    return report_list
      .filter(
        (item) =>
          item.brokers === Number(selectedBrokers) &&
          item.sale_no === Number(selectSell)
      )
      .filter(
        (item) =>
          new Date(item.timestamp).getFullYear() === new Date().getFullYear()
      )
      .sort((a, b) => a.id - b.id);
  }, [report_list, selectedBrokers, selectSell]);
  // console.log(catList);

  const soldList = useMemo(() => {
    return catList
      .filter((item) => item.sold_status === 1)
      .sort((a, b) => a.id - b.id);
  }, [catList]);
  
  const bidderReportdata = useMemo(() => {
    return report_list
      .filter((item) => item.buyer === users.link_id)
      .sort((a, b) => a.id - b.id);
  }, [report_list]);
  //soldstatus
  const sortedData5 = useMemo(
    () => [...soldList].sort((a, b) => a.id - b.id),
    [soldList]
  );

  const sortedData6 = useMemo(
    () =>
      [...sortedData5].sort((a, b) => {
        if (a.factory_name.toLowerCase() < b.factory_name.toLowerCase())
          return -1;
        if (a.factory_name.toLowerCase() > b.factory_name.toLowerCase())
          return 1;
        return b.id - a.id;
      }),
    [sortedData5]
  );
 
  const sortedData = useMemo(
    () => [...catList].sort((a, b) => a.id - b.id),
    [catList]
  );

  const sortedData2 = useMemo(
    () =>
      [...sortedData].sort((a, b) => {
        if (a.factory_name.toLowerCase() < b.factory_name.toLowerCase())
          return -1;
        if (a.factory_name.toLowerCase() > b.factory_name.toLowerCase())
          return 1;
        return b.id - a.id;
      }),
    [sortedData]
  );

  const sortedData3 = useMemo(
    () =>
      [...catList].sort((a, b) => {
        if (a.factory_name.toLowerCase() < b.factory_name.toLowerCase())
          return -1;
        if (a.factory_name.toLowerCase() > b.factory_name.toLowerCase())
          return 1;
        return b.id - a.id;
      }),
    [catList]
  );
  const brokers = factory_list.filter((factory) => factory.type === "BROKERS");

  const handleInputChange = (e) => {
    setSelectSell(e.target.value);
    const selectedData = sale_list.find((item) => item.sale_no === Number(e.target.value));

    // Update the selectedDataArray state with the selected data object
    setSelectedDataArray([selectedData]);
  };

  const handleSelectChange = (e) => {
    setSelectedBrokers(e.target.value);
    const selectedOption = brokers.filter(
      (shead) => shead.id === Number(e.target.value)
    );
    // console.log(selectedOption[0].name);
    setSelectedBrokersname(selectedOption[0].name);
  };
  useEffect(() => {
    dispatch(getReportdata());
    dispatch(getAllfactory());
    dispatch(getAllsaleno());
  }, [dispatch]);

  return (
    <>
      <section className="content">
        <section className="col-lg-12 connectedSortable mt-1">
          <div className="box box-primary">
            <div className="box-header header-custom">
              <div className="row">
                <div className="col-md-6 text-left">
                  <h3 className="box-title">Brokers Wise Report</h3>
                </div>
              </div>
            </div>

            <div className="container">
              <div className="row">
                <div className="col-6 col-md-6">
                  <div className="col-12 mb-2 col-md-12 d-flex flex-column align-items-center justify-content-center">
                  <select
       className="form-control"
       value={selectSell}
       onChange={handleInputChange}
      >
          <option value=""> -- Select Sale -- </option>
        {sale_list.map((item) => (
          <option key={item.sale_no} value={item.sale_no}>
            {`Sale No : `+ item.sale_no +`(`+item.season+`)`}
          </option>
        ))}
      </select>
                 
                   
                  </div>
                  <div className="col-12 col-md-12 d-flex flex-column align-items-center justify-content-center">
                    <select
                      required
                      value={selectedBrokers}
                      onChange={handleSelectChange}
                      name="factory"
                      autoComplete="off"
                      id="factory"
                      className="form-control"
                    >
                      <option value=""> -- Select Brokers -- </option>

                      {brokers.map((shead) => (
                        <option key={shead.id} value={shead.id}>
                          {shead.name}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>

                <div class="col-6 col-md-6 d-flex flex-column align-items-center justify-content-center">
                <CatalogueList
                    data={sortedData3}
                    user={selectedBrokersname}
                    saledetails={selectedDataArray}
                  />
                  <AuctionReport
                    data={sortedData2}
                    user={selectedBrokersname}
                    saledetails={selectedDataArray}
                  />
                       <SoldReport data={sortedData6} user={selectedBrokersname}   saledetails={selectedDataArray}/>
                 
                  {/* <OpenHTMLButton data={catList} user={selectedBrokersname}  saledetails={selectedDataArray} />
                 
                  <ValuationReport data={catList} user={selectedBrokersname}  saledetails={selectedDataArray}/>
                  <TestingReport data={catList} user={selectedBrokersname}  saledetails={selectedDataArray} />
             
                  <AuctionSummaryBuyer
                    data={sortedData6}
                    user={selectedBrokersname}
                    saledetails={selectedDataArray}
                  />
                  <FactorywiseSummary
                    data={catList}
                    user={selectedBrokersname}
                    saledetails={selectedDataArray}
                  />
                  <FactorySold data={catList} user={selectedBrokersname}  saledetails={selectedDataArray}/>
                  <InvoiceGenerate
                    sqlData={sortedData6}
                    user={selectedBrokersname}
                    saledetails={selectedDataArray}
                  />
                  <BrokersSummary
                    data={report_list}
                    user={selectedBrokersname}
                    saledetails={selectedDataArray}
                  /> */}
                  <BiddersoldReport
                    data={bidderReportdata}
                    user={users}
                    saledetails={selectedDataArray}
                  />
                </div>
              </div>
            </div>
          </div>
        </section>
      </section>
    </>
  );
};

export default ReportAll;
