import React from 'react';

const BiddersoldReport = ({ data,user,saledetails }) => {
  // Filter data where sold_status is 1
  const filteredData = data.filter((entry) => entry.sold_status === 1);

  // Calculate total amount, VAT, and average for each entry
  const reportData = filteredData.map((entry) => {
    const {brokers_name,  lot, invoice, factory_name, warehouse_name, pkgs, gross_total, maximum_bid } = entry;
    const amount = parseFloat(gross_total) * maximum_bid;
    const vat = amount * 0.15;
    const average = amount / parseFloat(gross_total);
    return {
      lot,
      invoice,
      factory_name,
      warehouse_name,
      pkgs,
      brokers_name, 
      gross_total: parseFloat(gross_total).toFixed(2),
      amount: amount.toFixed(2),
      total: (amount + vat).toFixed(2),
      avg: average.toFixed(2),
      vat: vat.toFixed(2),
    };
  });

  // Calculate the total amounts for each column
  const totalPkgs = reportData.reduce((total, entry) => total + parseInt(entry.pkgs), 0);
  const totalAmount = reportData.reduce((total, entry) => total + parseFloat(entry.amount), 0);
  const totalTotal = reportData.reduce((total, entry) => total + parseFloat(entry.total), 0);
  const totalAvg = (totalTotal / filteredData.length).toFixed(2);
  const totalVat = reportData.reduce((total, entry) => total + parseFloat(entry.vat), 0);
// Helper functions to calculate the totals for a specific broker
const totalPkgsForBroker = (brokerName) => {
  return reportData
    .filter((entry) => entry.brokers_name === brokerName)
    .reduce((total, entry) => total + parseInt(entry.pkgs), 0);
};

const totalAmountForBroker = (brokerName) => {
  return reportData
    .filter((entry) => entry.brokers_name === brokerName)
    .reduce((total, entry) => total + parseFloat(entry.amount), 0);
};

const totalTotalForBroker = (brokerName) => {
  return reportData
    .filter((entry) => entry.brokers_name === brokerName)
    .reduce((total, entry) => total + parseFloat(entry.total), 0);
};

const totalVatForBroker = (brokerName) => {
  return reportData
    .filter((entry) => entry.brokers_name === brokerName)
    .reduce((total, entry) => total + parseFloat(entry.vat), 0);
};
  // Convert the report data into HTML
  const reportAsHtml = reportData
    .map((entry,index) => {
      return `
        <tr>
          <td>${entry.lot}</td>
          <td>${entry.invoice}</td>
          <td style="text-align: left;">${entry.brokers_name}</td>
          <td style="text-align: left;">${entry.factory_name}</td>
          <td style="text-align: left;">${entry.warehouse_name}</td>
          <td style="text-align: right;">${entry.pkgs}</td>
          <td style="text-align: right;">${entry.gross_total}</td>
          <td style="text-align: right;">${entry.avg}</td>
          <td style="text-align: right;">${entry.amount}</td>
          <td style="text-align: right;">${entry.vat}</td>
          <td style="text-align: right;">${entry.total}</td>
        </tr>
        ${index === reportData.length - 1 || reportData[index + 1].brokers_name !== entry.brokers_name ? `
        <tr>
          <td colspan="5" style="text-align: right;">Sub Total:</td>
          <td style="text-align: right;">${totalPkgsForBroker(entry.brokers_name)}</td>
          <td></td>
          <td></td>
          <td style="text-align: right;">${totalAmountForBroker(entry.brokers_name).toFixed(2)}</td>
          <td style="text-align: right;">${totalVatForBroker(entry.brokers_name).toFixed(2)}</td>
          <td style="text-align: right;">${totalTotalForBroker(entry.brokers_name).toFixed(2)}</td>
        
         
        </tr>
      ` : ''}
      `;
    })
    .join('');

  const handleButtonClick = () => {
   
    const htmlContent = `
    <html>
    <head>
      <title>Bidder Report</title>
      <link rel="icon"  type="image/png" href="/ttab.png" />
      <style>
        .table-wrapper {
          margin: 0 auto;
          width: 1250px;
          padding: 20px;
          background-color: #ffffff;
        }
        body {
          font-family: "Courier New", Courier, monospace;
          color: #000;
          background: #757677;
          font-size: 12px;
        }
        .head {
          padding: 0px 50px;
          display: flex;
          justify-content: space-between;
        }
        table {
          width: 1200px;
          margin: 0 auto;
          border-collapse: collapse;
        }
        th,
        td {
          padding: 5px;
          border: 1px solid black;
          text-align: center;
        }
        th.align-right,
        td.align-right {
          text-align: right;
        }
        .total {
          font-weight: bold;
          color: purple;
        }
        .green-text {
          font-weight: 400;
          color: purple;
        }
        .red-text {
          color: red;
        }
        .season {
          display: flex;
          align-items: space-between;
          justify-content: center;
        }
        @media print {
          body {
            margin: 0;
            font-size: 10px;
          }
          .table-wrapper {
            page-break-inside: avoid;
          }
        }    .header {
         
          padding: 10px;
          display: flex;
          align-items: center;
          justify-content: space-between;
      }

      /* Icon in the first row */
      .icon {
          width: 60px;
          height: 60px;
             }

      /* Span Container */
      .span-container {
          display: flex;
          flex-direction: column;
          align-items: center; /* Center the container horizontally */
      }

      /* Individual Spans */
      .span1, .span2, .span3 {
          padding: 2px 10px;
          text-align: center;
      }

      .span1 {
          font-size: 26px;
          font-weight:bold;
      }

      .span2, .span3 {
          font-size: 14px;
      }
      </style>
    </head>
    <body>
    <div class="table-wrapper">
    <div class="header">
    <!-- Icon in the first row -->
    <div class="icon">
    <img class="icon" src="/ttab.png" alt="Icon Image" />
    </div>

    <!-- Span Container -->
    <div class="span-container">
        <span class="span1">${user.name}</span>
        <span class="span2">Season :${saledetails[0].season}</span>
        <span class="span3">Sale No : ${saledetails[0].sale_no}</span>
    </div>
    <div></div>
</div>
        <hr>
    <table>
    <thead>
      <tr>
        <th>Lot No</th>
        <th>Invoice No</th>
        <th>Broker Name</th>
        <th>Factory Name</th>
        <th>Warehouse Name</th>
        <th>Pkgs</th>
        <th>Gross Total</th>
      
        <th>Average</th>
        <th>Amount</th>
        <th>VAT 15%</th>
        <th>Total (Amount + VAT)</th>
       
      </tr>
    </thead>
    <tbody>
      ${reportAsHtml}
    </tbody>
    <tfoot>
      <tr>
        <td colspan="5" style="text-align: right;font-weight:bold;">Grand Total:</td>
        <td style="text-align: right;font-weight:bold;"> ${totalPkgs}</td>
        <td></td>
        <td></td>
        <td  style="text-align: right;font-weight:bold;">${totalAmount.toFixed(2)}</td>
        <td  style="text-align: right;font-weight:bold;">${totalVat.toFixed(2)}</td>
        <td  style="text-align: right;font-weight:bold;">${totalTotal.toFixed(2)}</td>
      
      </tr>
    </tfoot>
  </table>
      </div>
     
    </body>
  </html>
    `;

    const newWindow = window.open();
    newWindow.document.write(htmlContent);
    newWindow.document.close();
  };

  return (
  
      <button className="btn btn-primary w-100 text-center mb-2 d-flex justify-content-between align-items-center"  onClick={handleButtonClick}>
       
        <span className="text-center mx-auto">Bidder Report</span>
  <span className="icon my-auto">&#x1F512;</span>
      </button>
    
  );
};

export default BiddersoldReport;
